<template>
  <div class="collection">
    <div class="rg-top-top">
      <span class="content-top-text-blue"></span>
      <strong style="margin-left: 2px">{{company[0].shenbao_month}} 综合所得预扣预缴表</strong>
    </div>
    <div style="border: solid 1px #e0e0e0;margin: 0 10px;">
      <el-table :data="allIncomeTable" border style="width:100%">
        <el-table-column label="所得项目名称">
          <template slot-scope="scope">
        <span v-if="scope.row.isTrue" class="blue">
          <router-link :to="scope.row.path">{{scope.row.projectName}}</router-link>
        </span>
            <span v-else>
          {{scope.row.projectName}}
        </span>
          </template>
        </el-table-column>
        <el-table-column prop="addPersonNumber" label="填写人数"></el-table-column>
        <el-table-column prop="allIncomeNumber" label="收入额合计"></el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
             <span v-if="scope.row.isTrue" class="red">
          {{scope.row.status}}
        </span>
            <span v-else>
          {{scope.row.status}}
        </span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
        <span v-if="scope.row.isTrue" class="blue">
                <router-link :to="scope.row.path">{{scope.row.oprating}}</router-link>
        </span>
            <span v-else>
          {{scope.row.oprating}}
        </span>
          </template>
        </el-table-column>
      </el-table>

    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  components: {},
  data () {
    return {
      allIncomeTable: []// 综合所得预扣预缴表
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    this.setAllIncomeTable()
    this.$emit('pathNumber', 1)
  },
  computed: {
    ...mapGetters(['company'])
  },
  methods: {
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    nullClick () {
      this.showNull()
    },
    setAllIncomeTable () {
      let allIncomeTable = []
      let nameArr = ['正常工资薪金所得', '全年一次性奖金收入', '内退一次性补偿金', '年金领取', '解除劳动合同一次性补偿金', '央企负责人绩效薪金延期兑现收入和任期奖励', '单位低价向职工售房', '劳务报酬所得(保险营销员、证券经纪人)', '劳务报酬所得(一般劳务、其他劳务)', '稿酬所得', '特许权使用费所得', '提前退休一次性补贴', '个人股权激励收入']
      let pathArr = ['/normalSalary', '/yearSalary', '', '', '', '', '', '', '/laborSalary', '']
      for (let i in nameArr) {
        allIncomeTable[i] = {}
        if (i == 0 || i == 1 || i == 8) {
          allIncomeTable[i].isTrue = true
          allIncomeTable[i].path = pathArr[i]
          if (i == 0) {
            allIncomeTable[i].addPersonNumber = this.$store.state.form.normalForm.length
            allIncomeTable[i].status = allIncomeTable[i].addPersonNumber == 0 ? '未填写' : '已填写'
          }
          if (i == 1) {
            allIncomeTable[i].addPersonNumber = this.$store.state.form.yearForm.length
            allIncomeTable[i].status = allIncomeTable[i].addPersonNumber == 0 ? '未填写' : '已填写'
          }
          if (i == 8) {
            allIncomeTable[i].addPersonNumber = this.$store.state.form.laborForm.length
            allIncomeTable[i].status = allIncomeTable[i].addPersonNumber == 0 ? '未填写' : '已填写'
          }
        } else {
          allIncomeTable[i].addPersonNumber = 0
          allIncomeTable[i].status = '未填写'
        }
        allIncomeTable[i].projectName = nameArr[i]
        allIncomeTable[i].allIncomeNumber = 0
        allIncomeTable[i].oprating = '填写'
      }
      this.allIncomeTable = allIncomeTable
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #4285F4;
  }

  .el-table {
    font-size: 14px;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    line-height: 40px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    margin: 10px;
  }

  .top-div {
    flex: 1;
    background-color: #f4f6f9;
    color: #666666;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
  }
</style>



